const c5 = () => {
  const canvas = document.getElementById("c5");
  const ctx = canvas.getContext("2d");
  let x = 0;
  let y = 0;
  let timet = null;

  drawSin();

  function drawSin() {
    y = Math.sin(x);
    //y = Math.cos(x);
    //y = -x * x;
    //y = Math.tan(x);
    //y = Math.atan(x);
    // Ð¿Ð¾ÐºÐ¸ Ð² Ñ€Ð°Ð¼ÐºÐ°Ñ… ÐºÐ°Ð½Ð²Ð°ÑÐ°
    if (x >= 400) {
      x = 0;
    } else {
      // Ñ€ÐµÐºÑƒÑ€ÑÐ¸Ð²Ð½Ð¾ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÑŽ Ð´Ñ–Ñ‡
      x += 0.02;
    }
    ctx.fillRect(5 * x, 50 + 25 * y, 2, 2);
    ctx.fillRect(5 * x, 150 + x * y, 2, 2);
    timet = setTimeout(drawSin, 2);
  }
}; // c5

export default c5;
